@import url(https://fonts.googleapis.com/css?family=Londrina+Outline|Monofett&display=swap);
@import url(https://fonts.googleapis.com/css?family=Russo+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  background: #A7D2CB
}
/* HEADER */
.Header{
    margin-top:.5em;
    margin-bottom:.5em;
}



.btn-cocinero,.btn-mesero{
    text-transform: uppercase;
    padding: 30px 40px;
    border-radius: 15px;
    color:whitesmoke;
}
.btn-mesero{
   
    background-color: #c98474;
    
}
.btn-mesero:focus, .btn-mesero:hover{
    background: #81554A;
    color:whitesmoke;
}
.btn-cocinero:focus, .btn-cocinero:hover{
    background: #9B8757;
    color:whitesmoke;
}
.btn-cocinero{
    background-color: #f2d388;
}

.mesero, .cocinero{
    padding-top: 6em;
}
.mesero{
    padding-right: 30px;
    text-align: right;
}
.cocinero{
    padding-left: 30px;
    text-align: left;
}
.img-mesero, .img-cocinero{
    height: 8em;
    width: 8em;

}
@media (max-width: 425px) {
    .mesero{
        padding:1rem;
        text-align: center;
    }
    .cocinero{
        padding:1rem;
        text-align: center;
    }
    .img-mesero, .img-cocinero{
        height: 3em;
        width: 3em;
    
    }
}
/* TemplatWaiter */
.margin{
    margin-right: -10px;
}
.btn-desayuno, .btn-almuerzo, .btn-pedido, .btn-estado{
    font-size: 1.2em;
    font-family: 'Russo One', sans-serif;
    color:#A7D2CB;
    border: solid 1px;
}

.btn-desayuno{
    border-color: #c98474;
    background-color: #c98474;
}
.btn-almuerzo{
    border-color:#874c62;
    background-color: #874c62;
}
.btn-pedido{
    border-color: #c98474;
    background: #c98474;
    
   
}
.btn-estado{
    color:#A7D2CB;
    border-color:#874c62;
    background: #874c62
}
.btn-estado:focus, .btn-estado:hover{
    color: #c98474;
}
.btn-pedido:focus, .btn-pedido:hover{
    color: #874c62;
}
.btn-desayuno:focus,.btn-desayuno:hover{
    color:#874c62;
}
.btn-almuerzo:focus,.btn-almuerzo:hover{
    color:#c98474;
}
/* menu desayuno */
.desayuno{
    background: #c98474;
    margin-top: 5px;
    border-radius: 5px;
}
.desayuno .row:last-child{
    margin-bottom: 10px;
}
#btn1,#btn3{
   text-align: right;
   padding-right: 6px
}
#btn2,#btn4{
    text-align: left;
    padding-left: 6px
 }
.item{
    width:  100%;
    height:90%;
    background: #f2d388;
    margin-top:10px;
}
.coffee{
    width: 25%;
}
.sandwich, .juice, .coffee-cup{
    width: 30%;
}
.item p{
    color:#6c3d4e;
    font-family: 'Russo One', sans-serif;
    font-size: .7em;
}
/* meny almuerzo */
.almuerzo{
    background: #874c62;
    margin-top: 5px;
    border-radius: 5px; 
}
.almuerzo .col-4{
    padding:5px;
}
.almuerzo .col-3{
    padding: 5px;
}
.hamburger, .egg, .cheese, .potatoes, .rings, .drink{
    width: 45%;
}
.drinkBig{
    width: 60%;
}
/* pedido */
.pedido{
    background: #874c62;
    padding: 10px;
    margin-top: 5px;
    border-radius: 5px;
}
.pedido__input{
    border-radius: 5px;
    width: 100%;
}
.btn-order{
    width: 100%;
    font-family: 'Russo One', sans-serif;
    background: #f2d388;
    color:#874c62;
}
.btn-order:focus, .btn-order:hover{
    color:#c98474;
}
.btn-limpiar{
    background: #A7D2CB;
    width: 90%;
    font-family: 'Russo One', sans-serif;
    color: #874c62
}
.btn-limpiar:focus, .btn-limpiar:hover{
    color: #c98474;
}
table{
    font-size: 13px;
    font-family: 'Russo One', sans-serif;
    width: 100%;
    color:whitesmoke;
}
.tableOrder{
    margin-top:10px;
}
.nameItem{
    width: 60%;
}
.quantityItem{
    width:15%;
}
.priceTotalItem{
   width:20%; 
}
.deleteItem{
    width:5%; 
}
.total{
    font-size: 2em;
    width: 85%;
}
.totalPriceOrder{
    width: 15%;
    font-size: 2em;
}
.delete{
    width: 100%;
}
/* estado de pedidos */
@media (max-width: 425px) {
    .margin:nth-child(1){
        margin-top: 20px;
       
    }
}




.btn-pedidosChef{
    margin-top:1em;
    margin-bottom: 1em;
    background:#f2d388;
    font-size: 1.2em;
    font-family: 'Russo One', sans-serif;
    color:#874c62;
    border: solid 1px;
}
.btn-pedidosChef:hover,.btn-pedidosChef:focus{
    color:#c98474
}
.btn-chef{
    text-align: center;
}
.order-chef-items{
    font-family: 'Russo One', sans-serif;
    background:#874c62;
    border-radius:5px;
    border:solid 1px #874c62;;
    padding:5px;
    margin-bottom:1em;
}
.btn-listo{
    color:#874c62;
    background: #f2d388;
}
.btn-listo:focus, .btn-listo:hover{
    color:#c98474;
}
.order-chef-items h6{
    color: whitesmoke;
}

.btn-listo{
    margin-top:1em;
    padding: 0em 1em ;
}
